// c async await
export const getAuthUserData = () => async (dispatch) => {
   const response = await authAPI.me()
   if (response.data.resultCode === 0) {
      let { id, login, email } = response.data.data;
      dispatch(setAuthUserData(id, login, email, true)) //   actionCreator setAuthUserData
   }
}

export const login = (email, password, rememberMe, captcha, setStatus, setSubmitting) => async (dispatch) => { // thunkCreator  
   const response = await authAPI.login(email, password, rememberMe, captcha)  //   axios  .post
   if (response.data.resultCode === 0) {  //   ( ) - 
      dispatch(getAuthUserData()) //    ,       
      dispatch(getLoginErrorFromApi(null)) //   
      dispatch(getCaptchaUrlSuccess(null)) // url 
   } else {
      if (response.data.resultCode === 10) { //   10,   
         dispatch(getCaptchaUrl());
      }
      // let message = response.data.messages.length > 0 //  messages 
      //    ? response.data.messages[0]
      //    : 'Some error';
      // setStatus(message);   -  

      dispatch(getLoginErrorFromApi(response.data.messages[0]))
   };
   setSubmitting(false);
}

export const getCaptchaUrl = () => async (dispatch) => { // thunkCreator  
   const response = await securityAPI.getCaptchaUrl()  //   axios  .post
   const captchaUrl = response.data.url;
   dispatch(getCaptchaUrlSuccess(captchaUrl))
}

export const logout = () => async (dispatch) => { // thunkCreator  
   const response = await authAPI.logout()  //   axios  .post
   if (response.data.resultCode === 0) {
      dispatch(setAuthUserData(null, null, null, false)) //   
   }
}


export default authReducer;