import { usersAPI } from "../api/api";
import { updateObjectInArray } from "../utils/objects/helpers";

const FOLLOW = 'users/FOLLOW';
const UNFOLLOW = 'users/UNFOLLOW';
const SET_USERS = 'users/SET_USERS';
const SET_CURRENT_PAGE = 'users/SET_CURRENT_PAGE';
const SET_USERS_TOTAL_COUNT = 'users/SET_USERS_TOTAL_COUNT';
const TOGGLE_IS_FETCHING = 'users/TOGGLE_IS_FETCHING';
const TOGGLE_IS_FOLLOWING_PROGRESS = 'users/TOGGLE_IS_FOLLOWING_PROGRESS';

let initialState = {
   users: [],
   pageSize: 10,
   totalUsersCount: 0,
   currentPage: 1,
   isFetching: false,
   followingInProgress: []
}

const usersReducer = (state = initialState, action) => {
   switch (action.type) {

      //         . 
      //   helpers.js   updateObjectInArray

      /* case FOLLOW:
          return {
             ...state,
             users: state.users.map(u => {
                if (u.id === action.userId) {
                   return { ...u, followed: true }      // 
                }
                return u;
             })
          };
 
       case UNFOLLOW: {
          return {
             ...state,
             users: state.users.map(u => {
                if (u.id === action.userId) {
                   return { ...u, followed: false }      // 
                }
                return u;
             })
          };
       } */

      //
      case FOLLOW: {
         return {
            ...state,
            users: updateObjectInArray(state.users, action.userId, "id", { followed: true })
         };
      }

      case UNFOLLOW: {
         return {
            ...state,
            users: updateObjectInArray(state.users, action.userId, "id", { followed: false })
         };
      }

      case SET_USERS: {
         return {
            ...state,
            users: action.users
         }
      }

      case SET_CURRENT_PAGE: {
         return {
            ...state,
            currentPage: action.currentPage
         }
      }

      case SET_USERS_TOTAL_COUNT: {
         return {
            ...state,
            totalUsersCount: action.count
         }
      }

      case TOGGLE_IS_FETCHING: {
         return {
            ...state,
            isFetching: action.isFetching
         }
      }

      case TOGGLE_IS_FOLLOWING_PROGRESS: {
         return {
            ...state,
            followingInProgress: action.isFetching  //      ()
               ? [...state.followingInProgress, action.userId] //    id, -     
               : state.followingInProgress.filter(id => id != action.userId) //  id, -  action.userId
         }
      }

      default:
         return state;
   }
};

const followSuccess = (userId) => ({ type: FOLLOW, userId });  // , ..     users-reducer.js
const unfollowSuccess = (userId) => ({ type: UNFOLLOW, userId });  // , ..     users-reducer.js
const setUsers = (users) => ({ type: SET_USERS, users });  // , ..     users-reducer.js
const setCurrentPage = (currentPage) => ({ type: SET_CURRENT_PAGE, currentPage });  // , ..     users-reducer.js
const setUsersTotalCount = (totalUsersCount) => ({ type: SET_USERS_TOTAL_COUNT, count: totalUsersCount });  // , ..     users-reducer.js
const toggleIsFetching = (isFetching) => ({ type: TOGGLE_IS_FETCHING, isFetching });  // , ..     users-reducer.js
const toggleFollowingProgress = (isFetching, userId) => ({ type: TOGGLE_IS_FOLLOWING_PROGRESS, isFetching, userId });  // , ..     users-reducer.js

export const requestUsers = (page, pageSize) => async (dispatch) => {
   dispatch(setCurrentPage(page));  //   
   dispatch(toggleIsFetching(true));
   const data = await usersAPI.getUsers(page, pageSize)  //   axios  .get
   // data  response, ..  promise  response.data ( api.js)
   dispatch(toggleIsFetching(false));
   dispatch(setUsers(data.items));
   dispatch(setUsersTotalCount(data.totalCount));
}

